const web = [
    {
        path: '/web/register',
        name: '注册',
        component: resolve => require(['@/pages/web/Register'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/login',
        name: '登录',
        component: resolve => require(['@/pages/web/Login'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/home',
        name: '首页',
        component: resolve => require(['@/pages/web/Home'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/pro_details',
        name: '产品详情',
        component: resolve => require(['@/pages/web/ProductDetails'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/addToCart',
        name: '加入购物车',
        component: resolve => require(['@/pages/web/AddToCart'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/cart',
        name: '购物车',
        component: resolve => require(['@/pages/web/Cart'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: '/web/play',
        name: '结算',
        component: resolve => require(['@/pages/web/Play'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      }
]

export default web


