<template>
  <div id="app" class="default">
    <transition name="fade">
      <keep-alive>
        <router-view v-if="$route.meta.isKeepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition name="fade">
      <router-view v-if="!$route.meta.isKeepAlive"></router-view>
    </transition>
    <!-- <Loading :isLoading="getIsLoading" /> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: 'App',
  computed: {
    ...mapGetters(["getUserInfo", "getShopingCart"])
  },
  created() {
    if (this.getUserInfo.token&&!this.getShopingCart.length) {
      this.getCart();
    }
  },
  methods: {
    ...mapActions(['setShopingCart']),
    // 获取购物车
    getCart() {
      this.$api
        .getShopingCart(this.getUserInfo)
        .then(res => {
          let resData = res.data.data || []
          this.setShopingCart(resData)
        })
        .catch(err => console.log(err));
    }
  }
}
</script>

<style lang="less" scoped>
#app {
  font-size: 14px;
  line-height: 1.6;
}
</style>



