import * as type from './type'
import utils from '@/utils'

const actions = {
    // 用户信息
    setUserInfo({ commit }, obj) {
        // 刷新页面str为null
        utils.local.set('userInfo', obj);
        commit(type.SETUSERINFO, obj);
    },
    // 购物车
    setShopingCart({ commit }, obj) {
        // 刷新页面str为null
        utils.local.set('shopingCart', obj);
        commit(type.SETSHOPINGCART, obj);
    }

}

export default actions


