<template>
  <header class="header">
    <div class="login-register" v-if="isUserInfo">
      <div class="web clearfix">
        <div class="fl">
          <span class="welcome">您好，欢迎来到盛大健康平台！</span>
          <router-link class="login-btn" to="/web/login" v-if="!getUserInfo.token">登录</router-link>
          <router-link class="register-btn" to="/web/register" v-if="!getUserInfo.token">注册</router-link>
        </div>
        <div class="fr">
          <router-link class="btn" to="/vip/home">我的订单</router-link>
          <a class="btn" href="javascript:;">在线客服</a>
          <span class="btn">
            客服热线:
            <a href="tel:400-619-9069" class="red">400-619-9069</a>
          </span>
        </div>
      </div>
    </div>
    <div class="web logo-search clearfix">
      <router-link tag="h1" to="/" class="logo">
        <img src="@/assets/images/ximg14.png" alt="盛大健康" />
      </router-link>
      <p class="register-tip" v-if="isRegister">注册盛大健康账号</p>
      <div class="search-box" v-if="!isOther">
        <input class="input" type="text" />
        <a class="btn" href="javascript:;"></a>
      </div>
      <div class="cart" v-if="!isOther" @click="$router.push('/web/cart')">
        我的购物车
        <span class="num">{{cartNum}}</span>
      </div>
      <p class="to-login" v-if="isRegister">
        我已注册，现在就
        <router-link to="/web/login">登录</router-link>
      </p>
      <div class="tab-vip clearfix" v-if="isVip">
        <router-link to="/vip/home">会员中心</router-link>
        <router-link to="">账号安全</router-link>
        <router-link to="">购物车</router-link>
      </div>
    </div>
  </header>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  props: {
    isUserInfo: {
      type: Boolean,
      default: true
    },
    isOther: {
      type: Boolean,
      default: false
    },
    isRegister: {
      type: Boolean,
      default: false
    },
    isVip: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters(["getUserInfo", "getShopingCart"]),
    cartNum(){
      let num = 0;
      this.getShopingCart.map(item => {
        num += item.num;
      })
      return num;
    }
  },
};
</script>
<style lang="less" scoped>
@import "~@/less/mixins.less";
header {
  background-color: #fff;
  .login-register {
    padding: 10px 0;
    background-color: @grayBg;
  }

  .fl {
    width: 45%;
  }

  .welcome {
    margin-right: 10px;
  }

  .login-btn {
    margin-right: 5px;
  }

  .fr {
    width: 45%;
    text-align: right;

    .btn {
      margin-left: 30px;
    }

    .red {
      color: @themeColor;
      margin-left: 4px;
    }
  }

  .logo-search {
    position: relative;
    overflow: hidden;
  }

  .logo {
    float: left;
    width: 27.16%;
    margin: 44px 0 18px;
  }
  .register-tip {
    float: left;
    margin: 65px 0 0 28px;
    line-height: 30px;
    padding-left: 18px;
    border-left: 1px solid #ccc;
  }
  .to-login {
    float: right;
    margin-top: 60px;
    a {
      .btn(70px);
      display: inline-block;
      margin-left: 4px;
      vertical-align: middle;
    }
  }

  .search-box {
    .center();
    width: 43.16%;
    margin-left: 74px;
    border: 1px solid @themeColor;

    .input {
      width: 100%;
      padding-right: 70px;
      padding-left: 10px;
      height: 42px;
      line-height: 42px;
      font-size: 14px;
    }

    .btn {
      position: absolute;
      top: 0;
      right: 0;
      width: 70px;
      height: 42px;
      line-height: 42px;
      background: @themeColor url("~@/assets/images/ximg11.png") no-repeat
        center;
    }
  }

  .cart {
    .ycenter();
    right: 0;
    padding: 0 10px 0 40px;
    height: 42px;
    line-height: 42px;
    border: 1px solid @themeColor;
    color: @themeColor;
    background: url("~@/assets/images/ximg05.png") no-repeat 5px;
    background-size: 30px;

    .num {
      display: inline-block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: @themeColor;
      color: #fff;
      font-size: 12px;
      vertical-align: middle;
      line-height: 20px;
      text-align: center;
      margin-left: 4px;
    }
  }
  .tab-vip{
    float: left;
    margin: 40px 0 0 20px;
    padding: 20px 0;
    border-left: 1px solid #000;
    a{
      font-size: 20px;
      float: left;
      margin: 0 50px;
      &:hover{
        color: @themeColor;
      }
    }
  }
}
</style>



