import axios from './http'

// 获取手机验证码
export function getTelCode(args) {
    return axios.post('/api/sweb/web/user/sendsms', args);
}
// 注册
export function register(args) {
    return axios.post('/api/sweb/web/user/register', args, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    });
}
// 登陆
export function login(args) {
    return axios.post('/api/sweb/web/user/login', args, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    });
}
// 重置密码
export function resetPassWord(args) {
    return axios.post('/api/sweb/web/user/reset/pwd', args, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    });
}
//获取首页banner
export function getIndexBanner() {
    return axios.get('/api/sweb/web/index/lb/get');
}
//获取首页商品
export function getIndexGoods() {
    return axios.get('/api/sweb/web/index/goods/get');
}
//购物车
export function getShopingCart(args) {
    return axios.post('/api/sweb/web/goods/detail/one/shopingcart/get/list', args);
}
//商品详情
export function proDetails(args) {
    return axios.post('/api/sweb/web/goods/detail/one', args);
}
//加入购物车
export function addGood(args) {
    return axios.post('/api/sweb/web/goods/detail/one/shopingcart/create', args);
}
//更新购物车
export function updateCart(args) {
    return axios.post('/api/sweb/web/goods/detail/one/shopingcart/update', args);
}
//地址列表
export function addressList(args) {
    return axios.post('/api/sweb/web/user/address/list', args,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    });
}
//添加地址
export function addAddress(args) {
    return axios.post('/api/sweb/web/user/address/add', args,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    });
}
//支付
export function play(args) {
    return axios.post('/api/sweb/web/goods/detail/one/shopingcart/create', args);
}
// 银行卡
export function bankCard(args) {
    return axios.post('/api/sweb/web/user/card/list', args);
}
// 添加银行卡
export function addCard(args) {
    return axios.post('/api/sweb/web/user/card/bind', args);
}
// 提现记录
export function withdrawRecord(args) {
    return axios.post('/api/sweb/web/user/money/tx/list', args);
}
// 单个提现记录
export function withdrawOneRecord(args) {
    return axios.post('/api/sweb/web/user/money/tx/get/one', args);
}
// 提现信息
export function withdrawInfo(args) {
    return axios.post('/api/sweb/web/user/money/tx', args);
}
// 确认提现
export function withdraw(args) {
    return axios.post('/api/sweb/web/user/money/tx/commit', args);
}



