import Vue from 'vue'
import Router from 'vue-router'
import web from './web.js'
import vip from './vip.js'

Vue.use(Router)

export default new Router({
  routes: [
    ...web,
    ...vip,
    {
      path: '/',
      redirect: {
        name: '首页'
      }
    }
  ]
})



