// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import iView from 'iview'
// import VueAwesomeSwiper from 'vue-awesome-swiper'  //弃用
import 'iview/dist/styles/iview.css'
import '@/less/iview-theme.less'
import '@/less/reset.less'
// import 'swiper/dist/css/swiper.css'
import * as api from './api'
import utils from './utils'

// 全局组件
import vFooter from '@/components/web/VFooter'
import vHeader from '@/components/web/VHeader'
// import vLayout from '@/components/vip/VLayout'

Vue.component('v-footer', vFooter)
Vue.component('v-header', vHeader)
// Vue.component('v-layout', vLayout)

Vue.prototype.$utils = utils
Vue.prototype.$api = api

Vue.use(iView)
// Vue.use(VueAwesomeSwiper)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



