import Vue from 'vue'
import axios from "axios"
import qs from 'qs'
import { Notice } from 'iview'
import store from '@/store'
import router from '@/router'

axios.defaults.baseURL = window.config.APIBASEURL;
axios.defaults.timeout = window.config.APITIMEOUT;
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
// 跨域请求时是否需要使用凭证
axios.defaults.withCredentials = true;
// 添加请求拦截器
axios.interceptors.request.use(function (config) {
    // store.dispatch('setIsLoading', true)
    if (config.method === 'post') {
        config.data = Object.assign({},store.getters.getUserInfo, config.data || {})
        if(config.headers['Content-Type'] == "application/x-www-form-urlencoded"){
            config.data = qs.stringify(config.data);
        }
    }else if(config.method === 'get' && config.url.indexOf('?') == -1){
        config.url = config.url + '?time=' + new Date().getTime();
    }
    return config;
}, function (error) {
    // 对请求错误做些什么
    // store.dispatch('setIsLoading', false);
    return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    // store.dispatch('setIsLoading', false);
    let code = response.data.code;
    let msg = response.data.msg || response.data.message || "操作失败!"
    if(code == 0){
        return response;
    }else if(code == 10001){
        router.replace('/web/login')
        Notice.info({
            title: msg
        });
        return Promise.reject(msg)
    }else{
        Notice.info({
            title: msg||"网络错误"
        });
        return Promise.reject(0,msg);
    }
}, function (error) {
    // store.dispatch('setIsLoading', false);
    // 对响应错误做点什么
    // 请求超时
    let config = error.config;
    // If config does not exist or the retry option is not set, reject
    if(!config || !config.retry) return Promise.reject(error);
    
    // Set the variable for keeping track of the retry count
    config.__retryCount = config.__retryCount || 0;
    
    // Check if we've maxed out the total number of retries
    if(config.__retryCount >= config.retry) {
        // Reject with the error
        return Promise.reject(error);
    }
    
    // Increase the retry count
    config.__retryCount += 1;
    
    // Create new promise to handle exponential backoff
    let backoff = new Promise(function(resolve) {
        setTimeout(function() {
            resolve();
        }, config.retryDelay || 1);
    });
    
    // Return the promise in which recalls axios to retry the request
    return backoff.then(function() {
        return axios(config);
    });
});

export default axios


