const vip = [
  {
    path: '/vip',
    name: '会员',
    component: resolve => require(['@/pages/vip/Vip'], resolve),
    meta: {
      isKeepAlive: false,
      isInit: true //是否需要初始化
    },
    children: [
      {
        path: 'home',
        name: '首页',
        component: resolve => require(['@/pages/vip/Home'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: 'card',
        name: '银行卡',
        component: resolve => require(['@/pages/vip/Card'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: 'myRecharge',
        name: '我的充值',
        component: resolve => require(['@/pages/vip/MyRecharge'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: 'myWithdraw',
        name: '我的提现',
        component: resolve => require(['@/pages/vip/MyWithdraw'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: 'withdraw',
        name: '提现',
        component: resolve => require(['@/pages/vip/Withdraw'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },
      {
        path: 'setup',
        name: '安全设置',
        component: resolve => require(['@/pages/vip/SetUp'], resolve),
        meta: {
          isKeepAlive: false,
          isInit: true //是否需要初始化
        }
      },

    ]
  }
]
export default vip


