import * as type from './type'

const mutations = {
    // 用户信息
    [type.SETUSERINFO](state, obj){
        state.userInfo = obj
    },
    // 购物车
    [type.SETSHOPINGCART](state, obj){
        state.shopingCart = obj
    }

}

export default mutations


